import http from '../../http';

//获取新房交付信息
const listHouseDeliveDetail = (reqData) => {
    return new Promise((resolve, reject) => {
        http.post('/hisense-systemServer/app/owner/listHouseDeliveDetail.do',reqData,true, 'homeUrl').then(noteData => {
            resolve(noteData);
        }).catch(error => {
            reject(error);
        })
    });
}

