import {
  getAppPropertyListService,
  getAppLifeListService,
  getIfShowJieshenService
} from "../../services/propertyService";
import { queryAreaInfoService } from "../../services/propertyBillService";
import { isDelive } from "@/services/servers/DeliveryOfNewPremises";
import { getXihuAddrService } from "../../services/propertyService";
import { getUnpayBillNumService } from "../../services/homeService";
import { storage } from "../../utils/storage";
import NavigationBar from '@/common/currency/NavigationBar.vue';
import propertyNone from "../property/propertyNone";
import Vue from "vue";
import { Dialog } from "vant";
Vue.use(Dialog);
export const servicePage = {
  data() {
    return {
      defaultTitle: "暂无应用",
      userInfo: {},
      unpayBillNum: 0, //待缴账单数badge
      propertySerList: [], //物业应用列表
      isDefault:false,
      headerItemWidth:{
        width:'84px'
      },
      title:'服务',
      lifeSerList: [], //生活应用列表
      areaList: [] //缴费服务，判断页面跳转数据
    };
  },
  components: {
    propertyNone,
    NavigationBar
  },
  mounted() {
    this.getAppPropertyList();
    this.getJieshenService()
    this.getUnpayBillNum();
  },
  methods: {
    /* 获取物业服务 */
    getAppPropertyList() {
      let reqData = {
        AREA_CODE: this.$userInfo.AREA_CODE, //小区code
        OWNER_CODE: this.$userInfo.OWNER_CODE //业主code
      };
      getAppPropertyListService({ data: JSON.stringify(reqData) })
        .then(result => {
          //请求成功
          if (result != null) {
            this.isDefault = false;
            this.propertySerList = result;
            this.propertySerList.forEach((item, index) => {
              // 如果有手机开门,需要从列表中删除
              if ("0001" == item.APP_CODE) {
                this.propertySerList.splice(index, 1);
              }
            });
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.phoneDoorDefaule = true; //是否显示default页
          this.$toast("网络异常，请稍后重试！");
        });
    },
    // 获取当前小区是否展示衣物洗护服务
    getJieshenService(){
      let params = {areaCode:this.$userInfo.AREA_CODE}
      getIfShowJieshenService(params)
        .then(res=>{
          if (res.code != "0") {
            this.getAppLifeList()
          }else{
            //展示衣物洗护
            if (res.data == 1) {
              this.lifeSerList.push( {
                lifeShopName: "衣物洗护",
                lifePicUrl: require("../../assets/images/home/property/service_icon_laundry@2x.png"),
                flag: "true",
                APP_CODE: "22"
              });
            }
            this.getAppLifeList()
          }
        })
        .catch(error=>{
          this.getAppLifeList()
        })
    },
    /* 获取生活服务 */
    getAppLifeList() {
      let reqData = {
        areaCode: this.$userInfo.AREA_CODE //小区code
      };
      getAppLifeListService(reqData)
        .then(result => {
          //请求成功
          if (result.resultCode == "0") {
            if(result.data.lifeShopList.length >0){
              for (let index in result.data.lifeShopList) {
                this.lifeSerList.push(result.data.lifeShopList[index]);
              }
            }else{
              if(this.propertySerList.length <=0)
                this.isDefault = true;
            }
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.phoneDoorDefaule = true; //是否显示default页
          this.$toast("网络异常，请稍后重试！");
        });
    },
    //获取未缴费账单
    getUnpayBillNum: function() {
      let reqData = {
        userCode: this.$userInfo.OWNER_CODE
      };
      getUnpayBillNumService(reqData)
        .then(res => {
          if (res.resultCode == 200) {
            if (parseInt(res.data) > 0) {
              this.unpayBillNum = parseInt(res.data);
            }
          }
        })
        .catch(error => {
          this.$toast(JSON.stringify(error));
        });
    },
    /* 获取物业房产数量 */
    queryAreaInfo() {
      var reqData = {
        owner_code: this.$userInfo.OWNER_CODE
      };
      queryAreaInfoService({ data: JSON.stringify(reqData) })
        .then(result => {
          //请求成功
          console.log(JSON.stringify(result));
          if (result.result) {
            if (result.data.areaList.length <= 1) {
              this.$router.push({ name: "propertyBillList" });
            } else {
              this.$router.push({ name: "paymentRoomList" });
            }
          } else {
            this.$toast("网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    /* 跳转应用 */
    toAppDetail(appCode) {
      /* 需要登录，提示登录 */
      if (
        "" == this.$userInfo.OWNER_CODE ||
        undefined == this.$userInfo.OWNER_CODE
      ) {
        this.$router.push({ name: "login" }); //跳转登录
        return;
      }
      else {
        /* 需要绑定房间，提示绑定 */
        if (this.$userInfo.OWNER_CODE && this.$userInfo.AREA_NAME == "信我家智慧社区") {
          Dialog.confirm({
            message: "您需要绑定房产后才能使用对应的物业服务，如果您已绑定，请切换至该小区使用",
            cancelButtonText: "我知道了",
            confirmButtonText: "前往绑定"
          })
            .then(() => {
              //跳转到绑定房产页面
              this.$router.push({name: 'selectArea'});
            })
            .catch((e) => {
              // on cancel
              console.log("捕获到错误", e);
            });
          return;
        } else {
          if ("0002" == appCode) {
            this.$router.push({ name: "houseRental" }); //房产租售
          } else if ("0003" == appCode) {
            this.$router.push({ name: "engineeringList" }); //工程进展
          } else if ("0007" == appCode) {
            if (this.$devicePlatform == "pc") {
              this.$toast("仅支持在手机中使用。")
              return;
            }
            //智能家居
            var phone = this.$userInfo.PHONE;
            window.broadcaster.fireNativeEvent(
              "jumpToSmartHome",
              { phoneNum: phone },
              function() {}
            );
          } else if ("0011" == appCode) {
            this.$router.push({ name: "commonPhone" }); //常用电话
          }
          /*需要绑房间的应用 start*/
          if ("0004" == appCode) {
            //新房交付
            let deData = {
              OWNER_CODE: this.$userInfo.OWNER_CODE,
              ROOM_CODE: this.$userInfo.ROOM_CODE,
              AREA_CODE: this.$userInfo.AREA_CODE
            };
            deData = { data: JSON.stringify(deData) };
            isDelive(deData).then(
              res => {
                if (!res && typeof res != "undefined" && res != 0) {
                  this.$toast("暂无新房交付信息");
                  return;
                }
                if (1 == res.ENROL_FALG) {
                  //1 未报名
                  this.$router.push({
                    name: "deliverryOfNewPremises"
                  }); //未报名
                } else if (0 == res.ENROL_FALG) {
                  // 0 已报名
                  this.$router.push({
                    name: "deliverryOfNewPreDetail"
                  }); //未报名
                } else {
                  this.$toast("暂无新房交付信息");
                }
              },
              err => {
                this.$toast(err);
              }
            );
          } else if ("0005" == appCode) {
            //物业活动
            this.$router.push({
              name: "propertyActivityList"
            });
          } else if ("0006" == appCode) {
            //问卷调查
            this.$router.push({
              name: "questionnaireList"
            });
          } else if ("0008" == appCode) {
            //报修
            this.$store.dispatch("changeTypeFlag", 0);
            this.$router.push({
              name: "complaintReporting",
              params: { typeFlag: 0 }
            });
          } else if ("0009" == appCode) {
            //投诉
            this.$store.dispatch("changeTypeFlag", 1);
            this.$router.push({
              name: "complaintReporting",
              params: { typeFlag: 1 }
            });
          } else if ("0010" == appCode) {
            //缴费
            this.queryAreaInfo();
          } else if ("0012" == appCode) {
            //访客邀请
            this.$router.push({ name: "visitorInvite" });
          } else if ("0013" == appCode) {
            //视频监控
            this.$router.push({ name: "" });
          }else if("0023" == appCode){
            //语音助手
            this.$router.push({name:"voiceAssistant"});
          }
          /*需要绑房间的应用 end*/
        }
      }
    },
    /* 生活服务跳转 */
    toLifeDetail(app) {
      if (app.flag == "true") {
        //跳转到洁神
        this.openXihuPage();
      } else {
        //跳转后台下发的店铺
        this.$router.push({
          name: "shopPage",
          params: { shopCode: app.shopCode }
        });
      }
    },
    onClickLeft() {
      this.$router.back(-1);
      //this.$router.push("/homePage")
    },
    openXihuPage() {
      //获取衣物洗护的地址
      var data = {
        userId: this.$userInfo.PHONE
      };
      getXihuAddrService({ data: JSON.stringify(data) }).then(
        res => {
          //成功回调函数
          var addr = res.url;
          const platform = navigator.userAgent;
          const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
          if (!isiOS) {
            cordova.InAppBrowser.open(
              addr,
              "_blank",
              "location=yes,toolbar=yes,toolbarcolor=#ffffff,hideurlbar=yes,title=yes,titletext=洁神洗衣,titlecolor=#000000"
            );
          } else {
            cordova.InAppBrowser.open(
              addr,
              "_blank",
              "location=no,toolbar=yes,toolbarposition=top,toolbarcolor=#ffffff,title=yes,titletext=洁神洗衣,titlecolor=#000000"
            );
          }
        },
        error => {
          this.$toast("网页跳转失败，请稍后重试！");
        }
      );
    }
  }
};