//获取用户小区列表
const getAreaListService = (reqData) => {
    return new Promise((resolve, reject) => {
        http.get('xwj-property-house/house/room/getBindingRoomList', reqData, "baseUrl", false).then(noteData => {
            resolve(noteData);
        }).catch(error => {
            reject(error);
        })
    });
}

